.games {
    padding: 13px;
    background-color: #ffffff;
    border-radius   : 5px;
    box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.25);
}

.games__list {
    display        : flex;
    justify-content: center;
    flex-wrap      : wrap;
    padding        : 0;
    margin         : 0;
    list-style-type: none;
    color          : @white;
}

.games__item {
    display: flex;
    margin-bottom: 13px;
    text-align   : center;
    box-sizing   : border-box;

    &:last-child {
        margin-bottom: 0;
    }
}

.games_picture {
    margin-right: 9px;
    border-radius: 3px;
}

.games__title {
    margin       : 0;
    margin-bottom: 7px;
    font-size    : 12px;
    line-height  : 18px;
    color        : #828282;
}

.games__action {
    display: flex;
    flex-direction: column;
}

.games__action .button {
    width         : 100%;
    margin-bottom : 5px;
    padding-top   : 3px;
    padding-bottom: 4px;
    padding-left  : 0;
    padding-right : 0;
    font-size     : 12px;
    line-height   : 18px;
    text-transform: uppercase;
    box-sizing    : border-box;

    &:last-child {
        margin-bottom: 0;
    }
}

.games-banner img {
    border-radius: 5px;
}

@media (max-width: 970px) {
    .games {
        width: 100%;
        box-shadow: none;
    }
    .games__list {
        justify-content: flex-start;
    }

    .games__item {
        margin-right: 10px;
        margin-bottom: 10px;

        &:nth-child(3n) {
            margin-right: 0;
        }
    }

    .games-banner img {
        display: block;
        margin: 0 auto;
    }
}

@media (max-width: @tablet-width) {
    .games__list {
        justify-content: space-around;
    }

    .games__item {
        margin-right: 0;

        &:nth-child(4n) {
            margin-right: 0;
        }

        &:last-child {
            margin-bottom: 13px;
        }
    }
}

@media (max-width: 340px) {
    .games {
        padding-left: 0;
        padding-right: 0;
    }
}
