.review {
    width: 100%;
    margin-bottom: 34px;
    border-collapse: separate; 
    border-spacing: 0;
    border-radius: 6px;
}

.review__data tr {
    display: block;
    margin-bottom: 6px;
    border-radius: 5px;
    background-color: #ffffff;
    box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.25);

    &:first-child td {
        border-top: 0;
    }
}

.review__data .review__casino {
    width: 126px;
    background-color: #333333;
    text-align: center;
    border-radius: 5px 0px 0px 5px;
}

.review__account  {
    width: 173px;
}

.review__account .review__text {
    width: 85px;
}

.review__bonus .review__text {
    width: 135px;
}

.review__rating .review__text {
    width: 90px;
}

.review__data {
    border-radius: 6px;
}

.review__link {
    max-width: 126px;
    font-size: 14px;
    line-height: 21px;
    text-decoration: underline;
    color: #EFE5EB;

    &:hover {
        color: darken(#EFE5EB, 5%)
    }

    &:active {
        opacity: 0.5;
    }
}

.review__value {
    display: inline-block;
    margin-right: 8px;
    padding: 8px 12px;
    font-size: 14px;
    line-height: 21px;
    border-radius: 3px;
    border: 2px solid #BD1D65;
    color: #BD1D65;

    &--dark {
        padding: 8px 8px;
        border: 2px solid #333333;
        color: #333333;
    }

}

.review__text {
    display: inline-block;
    margin: 0;
    font-size: 14px;
    line-height: 105%;
    vertical-align: middle;
    text-align: left;
    color: #99838F;
}

.review__data td {
    position: relative;
    margin-right: 40px;
    padding-top: 10px;
    padding-bottom: 19px;
    text-align: center;
    word-wrap: break-word;

    &:last-child {
        margin-right: 0;
    }
}

.review__col-wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.review__action {
    padding-right: 12px;
    text-align: right;
}

.review__action .button {
    padding: 9px 26px;
}

@media (max-width: 1270px) {
    .review__text {
        text-align: center;
    }

    .review__text {
        margin-top: 5px;
    }

    .review__value {
        margin: 0;
    }

    .review__account {
        width: 140px;
    }
}

@media (max-width: @tablet-width) {
	table, tbody, th, td, tr { 
		display: block; 
    }

    .review__data td {
        margin-right: 0;
        padding-top: 12px;
        padding-bottom: 12px;
        border-top: 0;
    }

    .review__col-wrap {
        padding-left: 0;
    }

    .review__data .review__casino {
        padding-top: 12px;
        padding-right: 0;  
        border-radius: 5px 5px 0 0;
    }

    .review__link {
        max-width: 100%;
    }

    .review__data .review__text {
        width: 100%;
    }
    
    .review__data td, 
    .review__data .review__casino {
        width: 100%;
    }

    .review__action {
        padding-right: 0;
    }
	
	.review__data tr {
        border-radius: 6px;
        margin-bottom: 25px;
    }

    .review__rating {
        display: none;
    }

	td { 
		border: none;
		border-bottom: 1px solid darken(#FBE3FF, 70%); 
        position: relative;
        
        &:last-child {
            border-bottom: 0;
        }
    }
    
    .review__col-wrap {
        align-items: center;
    }

    .review .button {
        display: inline-block;
    }
}

@media (max-width: 460px) {
    .review__col-wrap {
        justify-content: center;
    }
}