.page-footer {
    padding-top   : 21px;
    padding-bottom: 20px;
    background    : linear-gradient(180deg, #40001C 0%, #BD1D65 8.85%, #BD1D65 89.58%, #40001C 100%);
}

.page-footer__wrap {
    display        : flex;
    justify-content: space-between;
    align-items: center;
    padding-left   : 195px;
    padding-right  : 197px;
}

.page-footer .main-nav__list {
    justify-content: flex-start;
}

.page-footer .main-nav__item {
    padding-top   : 9px;
    padding-bottom: 15px;
}

.page-footer__copyright {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.page-footer__copyright-text {
    font-size: 14px;
    line-height: 21px;
    color      : #EEB7D6;
}

@media (max-width: 1560px) {
    .page-footer__wrap {
        flex-direction: column;
        align-items   : center;
        padding-left: 20px;
        padding-right: 20px;
    }

    .page-foote .main-nav__item {
        margin-right: 25px;
    }
}

@media (max-width: @tablet-width) {
    .page-footer .main-nav__item {
        margin-right: 15px;
        border      : 0;

        &:last-child {
            margin-right: 0;
        }
    }

    .page-footer .main-nav__list {
        flex-direction: row;
        justify-content: center;
    }

    .page-footer .main-nav__link {
        font-size: 12px;
    }
}

@media (max-width: 460px) {
    .page-footer .main-nav__list {
        padding-left : 10px;
        padding-right: 10px;
    }

    .page-footer__logo img {
        width: 250px;
    }
}