.button {
    font-size     : 18px;
    font-weight   : bold;
    line-height   : 21px;
    text-transform: uppercase;
    border-radius : 5px;
    color         : #fff;
    transition    : 0.25s ease-out;
    cursor        : pointer;

    &--pink {
        padding    : 13px 31px;
        background : linear-gradient(180deg, #EA5172 0%, #BD1D65 100%);

        &:hover {
            background: linear-gradient(180deg, #BD1D65 0%, #EA5172 100%);
        }

        &:active {
            background: linear-gradient(180deg, darken(#BD1D65, 5%) 0%, darken(#EA5172, 5%) 100%);
        }
    }

    &--orange {
        padding      : 13px 31px;
        background   : linear-gradient(180deg, #FFCA00 0%, #FF9600 100%);
        border-radius: 5px;

        &:hover {
            background: linear-gradient(180deg, #FF9600 0%, #FFCA00 100%);
        }

        &:active {
            background: linear-gradient(180deg, darken(#FF9600, 5%) 0%, darken(#FFCA00, 5%) 100%);
        }
    }

    &--border {
        padding-top   : 1px;
        padding-bottom: 2px;
        border        : 2px solid #BD1D65;
        color: #BD1D65;

        &:hover {
            border-color: #FF9600;
            color: #FF9600;
        }

        &:active {
            border-color: darken(#FF9600, 10%);
            color: darken(#FF9600, 10%);
        }
    }

    &--header {
        position: relative;
        padding      : 1px 37px;
        line-height  : 27px;
        border: 1px solid #FFFFFF;
        border-radius: 5px;

        &::before {
            content: "";
            position: absolute;
            left: -4px;
            right: 0;
            top: -4px;
            bottom: 0;
            width: 105%;
            height: 121%;
            border: 1px solid #EA5172;
            border-radius: 7px;
        }
    }
}
