.content {
    margin-bottom: 40px;

    &--mb {
        margin-bottom: 44px;
    }

    &--mb-last {
        margin-bottom: 40px;
    }
}

.content h1 {
    margin       : 0;
    margin-bottom: 5px;
    font-size    : 33px;
    font-weight  : bold;
    line-height  : 49px;
    color        : #333333;
}

.content h2 {
    margin       : 0;
    margin-bottom: 13px;
    font-size    : 24px;
    font-weight  : bold;
    line-height  : 36px;
    color        : #333333;
}

.content p {
    margin: 0;
}

.content img {
    margin-right: 30px;
    border-radius: 10px;
}

.conten--flex {
    display: flex;
    flex-wrap: wrap;
}

.conten--flex img {
    height: 100%;
}

.conten--flex h2 {
    flex-basis: 100%;
    flex-grow: 1;
}

.conten--flex p {
    width: 64%;
}

.content p+h2 {
    margin-top: 30px;
}

@media (max-width: 1270px) {
    .conten--flex {
        justify-content: center;
    }

    .conten--flex p {
        width: 100%;
        text-align: center;
    }

    .conten--flex h2,
    .content h2,
    .content h1,
    .content p {
        text-align: center;
    }

    .conten--flex img {
        margin: 0;
        margin-bottom: 15px;
    }
}

@media (max-width: @tablet-width) {
    .content__baner img {
        display: block;
        margin-left: auto;
        margin-right: auto;
    }
    
}

@media (max-width: 520px) {
    .content h1 {
        font-size: 24px;
    }

    .content h2 {
        font-size: 20px;
    }
}

@media (max-width: 390px) {
    .content h1 {
        font-size: 19px;
    }

    .content h2 {
        font-size: 17px;
    }
}