.columns {
    display        : flex;
    justify-content: space-between;
}

.columns__content {
    width: 850px;
}

.columns__sidebar {
    width: 262px;
}


@media (max-width: 1270px) {
    .columns__content {
        width: 100%;
        padding-left: 15px;
        padding-right: 15px;
    }
}

@media (max-width: 970px) {
    .columns {
        flex-wrap: wrap;
    }

    .columns__content {
        padding-left: 0;
        padding-right: 0;
    }

    .columns__sidebar{
        width: 100%;
    }
}