  .content h2 {
    font-size: 17px;
  }
}
.games {
  padding: 13px;
  background-color: #ffffff;
  border-radius: 5px;
  box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.25);
}
.games__list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
  list-style-type: none;
  color: #ffffff;
}
.games__item {
  display: flex;
  margin-bottom: 13px;
  text-align: center;
  box-sizing: border-box;
}
.games__item:last-child {
  margin-bottom: 0;
}
.games_picture {
  margin-right: 9px;
  border-radius: 3px;
}
.games__title {
  margin: 0;
  margin-bottom: 7px;
  font-size: 12px;
  line-height: 18px;
  color: #828282;
}
.games__action {
  display: flex;
  flex-direction: column;
}
.games__action .button {
  width: 100%;
  margin-bottom: 5px;
  padding-top: 3px;
  padding-bottom: 4px;
  padding-left: 0;
  padding-right: 0;
  font-size: 12px;
  line-height: 18px;
  text-transform: uppercase;
  box-sizing: border-box;
}
.games__action .button:last-child {
  margin-bottom: 0;
}
.games-banner img {
  border-radius: 5px;
}
@media (max-width: 970px) {
  .games {
    width: 100%;
    box-shadow: none;
  }
  .games__list {
    justify-content: flex-start;
  }
  .games__item {
    margin-right: 10px;
    margin-bottom: 10px;
  }
  .games__item:nth-child(3n) {
    margin-right: 0;
  }
  .games-banner img {
    display: block;
    margin: 0 auto;
  }
}
@media (max-width: 768px) {
  .games__list {
    justify-content: space-around;
  }
  .games__item {
    margin-right: 0;
  }
  .games__item:nth-child(4n) {
    margin-right: 0;
  }
  .games__item:last-child {
    margin-bottom: 13px;
  }
}
@media (max-width: 340px) {
  .games {
    padding-left: 0;
    padding-right: 0;
  }
}
.columns {
  display: flex;
  justify-content: space-between;
}
.columns__content {
  width: 850px;
}
.columns__sidebar {
  width: 262px;
}
@media (max-width: 1270px) {
  .columns__content {
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
  }
}
@media (max-width: 970px) {
  .columns {
    flex-wrap: wrap;
  }
  .columns__content {
    padding-left: 0;
    padding-right: 0;
  }
  .columns__sidebar {
    width: 100%;
  }
}
.button {
  font-size: 18px;
  font-weight: bold;
  line-height: 21px;
  text-transform: uppercase;
  border-radius: 5px;
  color: #fff;
  transition: 0.25s ease-out;
  cursor: pointer;
}
.button--pink {
  padding: 13px 31px;
  background: linear-gradient(180deg, #EA5172 0%, #BD1D65 100%);
}
.button--pink:hover {
  background: linear-gradient(180deg, #BD1D65 0%, #EA5172 100%);
}
.button--pink:active {
  background: linear-gradient(180deg, #a71a59 0%, #e73a60 100%);
}
.button--orange {
  padding: 13px 31px;
  background: linear-gradient(180deg, #FFCA00 0%, #FF9600 100%);
  border-radius: 5px;
}
.button--orange:hover {
  background: linear-gradient(180deg, #FF9600 0%, #FFCA00 100%);
}
.button--orange:active {
  background: linear-gradient(180deg, #e68700 0%, #e6b600 100%);
}
.button--border {
  padding-top: 1px;
  padding-bottom: 2px;
  border: 2px solid #BD1D65;
  color: #BD1D65;
}
.button--border:hover {
  border-color: #FF9600;
  color: #FF9600;
}
.button--border:active {
  border-color: #cc7800;
  color: #cc7800;
}
.button--header {
  position: relative;
  padding: 1px 37px;
  line-height: 27px;
  border: 1px solid #FFFFFF;
  border-radius: 5px;
}
.button--header::before {
  content: "";
  position: absolute;
  left: -4px;
  right: 0;
  top: -4px;
  bottom: 0;
  width: 105%;
  height: 121%;
  border: 1px solid #EA5172;
  border-radius: 7px;
}
.review {
  width: 100%;
  margin-bottom: 34px;
  border-collapse: separate;
  border-spacing: 0;
  border-radius: 6px;
}
.review__data tr {
  display: block;
  margin-bottom: 6px;
  border-radius: 5px;
  background-color: #ffffff;
  box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.25);
}
.review__data tr:first-child td {
  border-top: 0;
}
.review__data .review__casino {
  width: 126px;
  background-color: #333333;
  text-align: center;
  border-radius: 5px 0px 0px 5px;
}
.review__account {
  width: 173px;
}
.review__account .review__text {
  width: 85px;
}
.review__bonus .review__text {
  width: 135px;
}
.review__rating .review__text {
  width: 90px;
}
.review__data {
  border-radius: 6px;
}
.review__link {
  max-width: 126px;
  font-size: 14px;
  line-height: 21px;
  text-decoration: underline;
  color: #EFE5EB;
}
.review__link:hover {
  color: #e5d5df;
}
.review__link:active {
  opacity: 0.5;
}
.review__value {
  display: inline-block;
  margin-right: 8px;
  padding: 8px 12px;
  font-size: 14px;
  line-height: 21px;
  border-radius: 3px;
  border: 2px solid #BD1D65;
  color: #BD1D65;
}
.review__value--dark {
  padding: 8px 8px;
  border: 2px solid #333333;
  color: #333333;
}
.review__text {
  display: inline-block;
  margin: 0;
  font-size: 14px;
  line-height: 105%;
  vertical-align: middle;
  text-align: left;
  color: #99838F;
}
.review__data td {
  position: relative;
  margin-right: 40px;
  padding-top: 10px;
  padding-bottom: 19px;
  text-align: center;
  word-wrap: break-word;
}
.review__data td:last-child {
  margin-right: 0;
}
.review__col-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.review__action {
  padding-right: 12px;
  text-align: right;
}
.review__action .button {
  padding: 9px 26px;
}
@media (max-width: 1270px) {
  .review__text {
    text-align: center;
  }
  .review__text {
    margin-top: 5px;
  }
  .review__value {
    margin: 0;
  }
  .review__account {
    width: 140px;
  }
}
@media (max-width: 768px) {
  table,
  tbody,
  th,
  td,
  tr {
    display: block;
  }
  .review__data td {
    margin-right: 0;
    padding-top: 12px;
    padding-bottom: 12px;
    border-top: 0;
  }
  .review__col-wrap {
    padding-left: 0;
  }
  .review__data .review__casino {
    padding-top: 12px;
    padding-right: 0;
    border-radius: 5px 5px 0 0;
  }
  .review__link {
    max-width: 100%;
  }
  .review__data .review__text {
    width: 100%;
  }
  .review__data td,
  .review__data .review__casino {
    width: 100%;
  }
  .review__action {
    padding-right: 0;
  }
  .review__data tr {
    border-radius: 6px;
    margin-bottom: 25px;
  }
  .review__rating {
    display: none;
  }
  td {
    border: none;
    border-bottom: 1px solid #6b007d;
    position: relative;
  }
  td:last-child {
    border-bottom: 0;
  }
  .review__col-wrap {
    align-items: center;
  }
  .review .button {
    display: inline-block;
  }
}
@media (max-width: 460px) {
  .review__col-wrap {
    justify-content: center;
  }
