.rating {
  margin: 0;
  padding: 0;
  border: 0;
}
.rating__group {
  position: relative;
  width: 70px;
  height: 14px;
  background-image: url("../img/star-off.svg");
  background-size: 14px auto;
  background-repeat: repeat-x;
}
.rating__star {
  position: absolute;
  top: 0;
  left: 0;
  margin: 0;
  height: 14px;
  font-size: inherit;
  appearance: none;
  background-size: 14px auto;
  background-repeat: repeat-x;
}
.rating__star:focus {
  outline: none;
}
.rating__star:focus ~ .rating__focus {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  outline: 0.2em solid #4a90e2;
  outline-offset: 0.2em;
}
.rating__star:hover,
.rating__star:checked {
  background-image: url("../img/star-on.svg");
}
.rating__star:hover ~ .rating__star {
  background-image: url("../img/star-off.svg");
}
.rating__star:nth-of-type(1) {
  z-index: 5;
  width: 14px;
}
.rating__star:nth-of-type(2) {
  z-index: 4;
  width: 28px;
}
.rating__star:nth-of-type(3) {
  z-index: 3;
  width: 42px;
}
.rating__star:nth-of-type(4) {
  z-index: 2;
  width: 56px;
}
.rating__star:nth-of-type(5) {
  z-index: 1;
  width: 70px;
}
@media (max-width: 1270px) {
  .rating {
    display: none;
  }
}
@media (max-width: 970px) {
  .rating {
    display: block;
  }
}
